<template>
  <div class="page">
      <img class="logo" src="logo.png">
      <br>
        <span class="search_wrapper">
        <v-autocomplete
          :items="items"
          v-model="item"
          :get-label="getLabel"
          :component-item="template"
          :min-len="2"
          @update-items="updateItems"
          @keyup="test"
        ></v-autocomplete>
      </span>
  </div>
</template>

<script>
// @ is an alias to /src
import ItemTemplate from "../components/ItemTemplate.vue";
import axios from "axios";

export default {
  data() {
    return {
      item: null,
      items: [],
      template: ItemTemplate
    };
  },
  mounted: function(){
    const el = document.querySelector('.v-autocomplete-input')
    const that = this
    el.addEventListener('keyup', e => {
      if(e.keyCode == 13){
        window.query = `${e.target.value}`
        that.$router.push({name:'about'})
      }
    })
    el.focus()
  },
  methods: {
    getLabel(item) {
      return item ? item.name : "";
    },
    updateItems(text) {
      this.fetchFromElastic(text).then(response => {
        if (response.status == 200) {
          this.items = this.convertList(response);
        }
      });
    },
    fetchFromElastic(txt) {
      return axios.post(
        "https://api.snhyun.me:8881/autocompletion/_search",
        {
          query: {
            term: {
              "content.keyword_prefix": `${txt}`
            }
          },
          highlight: {
            fields: {
              "content.keyword_prefix": {}
            }
          }
        },
        { headers: { "Content-Type": "application/json" } }
      );
    },
    convertList(res) {
      debugger;
      return res.data.hits.hits.map(r => ({ 
        html: r.highlight['content.keyword_prefix'][0],
        name: r._source.content 
      }));
    },
    test(){
      debugger;
    }
  }
}




</script>

